.page-content {
    .richtext {
        h1, h2, h3, h4, h5, h6 {
            &:not(:last-child) {
                margin-bottom: 0.5rem;
            }
        }

        &.md-content {
            overflow: hidden;
        }

        & > .mg-container {
            overflow: visible;
        }
    }
}